.border-toptrick{
    position: absolute;
    height: calc(100% - 65px);
    width: 0;
    left: 15px;
    border: solid 3px #17181a;
}
.border-righttrick{
    position: absolute;
    height: 0;
    top: 8px;
    width: calc(100% - 34px);
    border: solid 3px #17181a;
}
.border-lefttrick{
    position: absolute;
    height: 0;
    top: calc(100% - 79px);
    width: calc(100% - 34px);
    border: solid 3px #17181a;
}
.border-bottomtrick{
    position: absolute;
    height: calc(100% - 65px);
    left: calc(100% - 19px);
    width: 0;
    border: solid 3px #17181a;
}
