.table-container{
    white-space: nowrap;
    width: 100%;
    max-height: 53.2vh;
    overflow: hidden;
    overflow-x: auto;
    overflow-y: auto;
    animation-duration: 3s;
}
::-webkit-scrollbar {
  width: 1em;
  height: 1em
}
table {
    font-weight: 300;
    font-family: arial, sans-serif;
    border-collapse: collapse;
    font-size: 0.95rem;
  }
  td, th {
    font-weight: 300;
    border: 1px solid #353535;
    text-align: left;
    padding: 6px;
    line-height: 1.5rem;
  }
  td input{
    font-size: 0.9rem;
  }
  /* tr:nth-child() {
    background-color: #888888;
  } */
  tr:hover{
    background-color: rgba(175, 175, 175, 0.1);
  }
  tr:nth-child(0):hover {
    background-color: #17181a;
  }
  td:last-child{
    text-align: center;
    cursor: pointer;
  }
  input{
    background:none;
    color: white;
    width: 100%;
    text-align: left;
    font-size: 1rem;
    border: none;
    outline: none;
  }
  input::hover{
    background: none;
    border: none;
  }
  .inputStyle{
   border-bottom: solid 1px white;
   animation: inputEffect 1s;
   padding-bottom: 2px;
   outline: none;
  }
  @keyframes inputEffect
  {
    from {width:0% ;}
    to {width:100% ;}
  }

